@import url('https://fonts.googleapis.com/css2?family=Lilita+One&family=Martian+Mono:wght@600&family=Play:wght@700&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Set body styles */
body {
    font-family: Arial, sans-serif;
    height: 1000px;
    overflow-y: scroll;
    display: grid;
    /* padding: 19px; */
    /* background-image: url(./waves-in-norways-lofoten-islands-fz.jpg); */
    /* background-repeat: no-repeat;
    background-size: cover; */
}
.top{
    height: 14%;
    /* display: grid; */
    display: flex;
    width: 100%;
    /* grid-template-columns: 1fr 1fr 1fr;LISTEN FUTURE VIVEK CHECKING IT..ITS A COLUMNS CONTENT DEVIVEDER WITH EQUAL DRISTRIBUTION OF SPACE LIKE 1fr MEANS 1PART AND OTHERS WITH MORE HAVING 2fror more VALUE */
    align-items: center;
    /* padding: 5px; */
    /* border-radius: 20px; */
    position: fixed;
    padding: 2%;
    gap: 1%;
    background-color: inherit;
    justify-content: space-between;
}

.top:hover{
    /* box-shadow: 5px 5px 15px rgb(36, 48, 76); */
    background-color: rgb(243, 241, 241);
    transition: all 0.2s ease-in-out;
    box-shadow: 5px 5px 15px rgb(36, 48, 76);

}


.profimg{
    height: 60px;
    width: 60px;
    border-radius: 15%;
    border: 1px solid;
}

.viv{
    font-family: 'Play';
    color: rgb(0, 0, 0);
    text-shadow: 3px 3px 2px rgb(255, 255, 255);
    font-weight: 700;
    font-size: 29px;
    margin-left: -14%;
} 
