.info{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.mennu{
    /* justify-content: space-between; */
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-right: 10%;
}

.mennu h1 a{
    text-decoration: none;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 18px;
    color: rgb(29, 28, 28);
    padding: 10%;
    border-radius: 10px;
    background-color: azure;
}
.mennu h1 a:hover{
color: rgb(29, 28, 28);
    padding: 9%;
    border-radius: 10px;
    /* background-color: rgb(219, 219, 219);} */}
.first{
    height: 800px;
    margin-top: 8%;
    background-image: url(./waves-in-norways-lofoten-islands-fz.jpg);
    background-repeat: no-repeat;
    background-size:cover;
    width: 100%;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1%;
}
.first h1{
    font-family: 'Martian Mono';
    border: 2px solid;
    font-size: 40px;
    color: rgb(255, 255, 255);
    background-color: inherit;
    border-radius: 10%;
    position: static;
}
.app{
    height: 9000px;
    background-color: azure;
}
.menuimg{
    width: 50px;
    height: 50px;
    cursor: pointer;
}
.closing{
    width: 20px;
    height: 20px;
}
.ot{
    padding-top: 20%;
}
.Menu{
    margin-top: 30%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 99%;
    background-color: rgb(243, 241, 241);
    padding: 25%;
    border: 1px solid;
    border-radius: 40px 40px 40px 40px;
    transition: all 0.5s ease;
}
.opt h2 a{
    text-decoration: none;
    font-family: 'Play';
    color: black;
    font-size: x-large;
}
.opt{
    gap: 20%;
    padding: 10%;
}